function updateLog(str){
    $('.logtext').html(str);
}    


$(document).ready(function(){
    // var systemReportStr = '<h2 class="titleText">System Report</h2>';
// var screenSize = "<b class='titleText0'>Screen Size:</b> <br/>Width:"+$(window.opener).width() +", Height:"+$(window.opener).height();
// var screenInfo = getS();


    window.onunload  = function(){					
        window.opener.closeCommunicationWin();
    }

    updateLog(window.opener.storageComms.getLogString());				

    $('.filter').on('click', function(){	
        $(this).toggleClass('active');
        // var $dataItems = $('.data-item.data-'+$(this).data('item')+'-item')
        if ($(this).hasClass('active')){
            $('.logtext').addClass('show-'+$(this).data('item'))
        } else {
            $('.logtext').removeClass('show-'+$(this).data('item'))
        }
    });
});